___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, Helvetica, sans-serif;
}

.app {
  display: flex;

  ul {
    list-style-type: none;
  }
}

dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  min-width: 400px;
  min-height: 100px;
  text-align: center;
  justify-content: center;
  padding: 30px;
}

.todo-item-modal form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  text-align: center;
}

.confirm-modal__buttons {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.sidebar {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 20px;
  font-size: 1.5rem;
}

.sidebar__sections {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  gap: 20px;
}

.sidebar__project-list{
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar__project-list-title {
  display: flex;
  justify-content: space-between;
  margin: 10px 0px;
}

.sidebar__project-list li {
  display: flex;
  justify-content: space-between;
}

.sidebar__project {
  flex: 1;
  min-height: 30px;
  padding: 10px;
  text-align: start;
}

.content {
  flex: 5;
  padding: 50px;
}

.project ul {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.todo {
  display: flex;
  align-items: start;
}

.todo [type="checkbox"] {
  margin: 10px 0px 0px 10px;
  height: 1.1rem;
}

.todo__content {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 10px;
  gap: 10px;
}

.todo__header {
  display: flex;
  gap: 20px;
}

.todo__title {
  flex: 1;
}
